@import "../core/variables";

.pokemon {
    &__item {
        margin-bottom: 1rem;
        width: 12rem;
        list-style: none;
        border-radius: 0.5rem;
        border: 1px solid #ddd;
        box-shadow: 0px 0px 11px 12px #a040a0;
        font-weight: bold;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;

        & > * {
            margin: 3px 0;
        }
    }
    &__img {
        width: 200px;
        height: 200px;
    }

    &__label {
        display: inline-block;
        font-weight: normal;
        width: 5rem;
        color: #fff;
        margin-bottom: 1rem;
        text-decoration: underline;
    }

    &__name {
        text-transform: capitalize;
        text-align: center;
        font-weight: bold;
    }
    &__type {
        border: solid 1px white;
        border-radius: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 1rem;
        list-style: none;
        font-family: $font-first;
        padding-bottom: 0.3rem;
    }
}

